import React, { useState, useEffect } from 'react';
import axios from 'axios';
import TablaCarreras from './../tables/TablaCarreras.jsx';
import TablaCrr from './../tables/TablaCrr.jsx';







function traducirTipo(tipo) {
  switch (tipo) {
    case 'F':
      return 'Ready';
    case 'R':
      return 'Resting';
    case 'S':
      return 'Waiting Sell';
    case 'I':
      return 'Registered';
    case 'P':
      return 'Pending registration';
    default:
      return 'Unknown';
  }
}

function carreras_list(T,W,C){
 let carreras=""
 if(T>0){
    if(W>0){
     if(W===1){carreras="G1"}
     if(W===2){carreras="G2"}
     if(W===3){carreras="G3"}
     if(W===4){carreras="G4"}
     if(W===5){carreras="G5"}
     if(W>5){
      if(C===0){carreras="PC"}
      if(C===1){carreras="C1"}
      if(C===2){carreras="C2"}
      if(C===3){carreras="C3"}
      if(C===4){carreras="C4"}
      if(C===5){carreras="C5"}
      if(C> 5){carreras="C6"} } 
      }else if(W===0){
        carreras="P0"
    }
  }else{
    carreras="D0"
  }

return carreras 

}


const ModalRace = ({ children, isOpen,openModal, closeModal, propsi }) => {
  const [data, setData] = useState(null);
   const [isLoadingData, setIsLoadingData] = useState(true);
  const [isCancelledData, setIsCancelledData] = useState(false);
   const [equino, setEquino] = useState(null);
   const [isLoadingEquino, setIsLoadingEquino] = useState(true);
   const [isCancelledEq, setIsCancelledEq] = useState(false);
  const [loading_race_status_create, setLoading_race_status_create] = useState(false);
    const [schedule, setSchedule] = useState([]);
   const [races, setRaces] = useState([]);
  const [showData, setShowData] = useState(false);
  const [timeActual, setTimeActual] = useState(null);
  let identidad=propsi.identidad;
  console.log("EEEEEQUINOX",equino)
  console.log("EEEEEQUINOX PROSI",propsi)








 useEffect(() => {
 if (isOpen){
  axios.get('./race?status=create')
  .then(response => setData(response.data.body))
  .then(()=>  axios.get(`./equino?equineId=${propsi.identidad}`))
  .then((equino)=> setEquino(equino.data.body)) 

}else{
  setData(null);
  setEquino(null);
}
  
  }, [isOpen]);






  const handleModalContainerClick = (e) => e.stopPropagation();

    


    
const handleFetch = async (propsi) => {

  if(!showData){
  
  let c_totales=propsi.carrerasTotals;
  let c_win=propsi.ganadasTotals
  let c_win_c=propsi.ganadasCon
  let carreras=carreras_list(c_totales,c_win,c_win_c)
  let Schedule_race=0;
  try {
  
  if(equino[0].estado==="registrado" ){
  
  Schedule_race=equino[0].schedule
  console.log("shedul",Schedule_race)
  for (let i = data.length - 1; i >= 0; i--) {
   
     console.log("dataraceid",data[i].raceid)
  






      data.forEach((das)=>{console.log("DAShedule",das)
        console.log(das.raceid)
        console.log(Schedule_race)
 if (das.raceid===Schedule_race){
  
 setSchedule((schedule) => [, das ] );
 setRaces(null)
   }
  })
  }}else if(equino[0].estado==="Ready" || "Procesando Inscripcion"){
  
  data.forEach((das)=>{console.log("DATASDES",das)
  let tip1=das.tipo.substr(0,2);
  let tip2=das.tipo.substr(3,2);
  let tip3=das.tipo.substr(6,2);
   console.log("TIP1",tip1);
    console.log("TIP2",tip2);
     console.log("TIP3",tip3);
  if((tip1===carreras)||(tip2===carreras)||(tip3===carreras)){
     console.log("CARRE",carreras);
  
  setRaces((races) => [...races, das]);
 setSchedule(null)
   }
  })
  
  
  }}
  
  
  catch(error){
    console.log("soy EL CATCH")
    console.log("error de axio",error)

  }
  
 }else{   
  setRaces([])
  setSchedule([])
  }




       // })

if ((data)&& (showData === false)){
setShowData(true) }else{   setShowData(false)}





};







  return (
    <article className={`modal ${isOpen && "is-open"}`} onClick={closeModal}>
      <div className="modal-container" onClick={handleModalContainerClick}>
        <button className="modal-close" onClick={closeModal}>
          X
        </button>
         <div className="card-info">
        
       
          <div className="content"> 
       {equino && equino[0] && equino[0].status ? (
                    <p className="Status">
                        {equino[0].status.split(',').map(traducirTipo).join(', ')}{' '}
                        
                    </p>
                ) : (
                    <p className="Status">
                        {propsi.status.split(',').map(traducirTipo).join(', ')}
                    </p>
                )}
           <div className="content-img"> 
         <img src={propsi.Imagen} alt={propsi.name} className="icono"  />

      
         </div>
            <div className="name">
          <p>{propsi.name}{propsi.TimeActual}</p>
         </div>
                 <div className="data-container" style={{ display: showData ? 'block' : 'none' }}>
{data && (

   schedule ?(
 <>   
 <TablaCrr data={schedule} propsi={propsi} fecha={timeActual} />
      
   </>
  ) : (
      <TablaCarreras data={races} propsi={propsi} fecha={timeActual} />
      
  )
)}
        </div>
         <div className="actions">
<input type="submit" name="background"  value="Ver Carreras Disponibles"  onClick={() => handleFetch(propsi) }/>


</div>
       </div>
       </div>
        </div>

    </article>
  );
};

export default ModalRace;
