import { useFormLogin } from "../../hooks/useFormLogin.jsx";
import iconCorriendoAndoL from'./../../../assets/gif/running-horse-horse-racing.gif'


const initialForm = {
  name: "",
 };

const validationsForm = (form) => {
  let errors = {};
  let regexName = /^[A-Za-z0-9\s]+$/;
 if (!form.name.trim()) {
    errors.name = "Fill in this field";
  } else if (!regexName.test(form.name.trim())) {
    errors.name = "The field does not accept  special characters";
  }
 return errors;
};

let styles = {
  fontWeight: "bold",
  color: "#dc3545",
};

const LoginForm = () => {

 const {
    form,
    errors,
    handleChange,
    handleBlur,
    handleSubmit,
  } = useFormLogin(initialForm, validationsForm);

  return (
    <div className="container-login">
      <h2>Ingresa tu usuario Hive</h2>
      <form onSubmit={handleSubmit}>
        <input
          type="text"
          name="name"
          placeholder="Type your username"
          onBlur={handleBlur}
          onChange={handleChange}
          value={form.name}
          required
        />
       
        {errors.name && <p style={styles}>{errors.name}</p>}
        <input type="submit"  value="Go" />
       
      </form>
       <img src={iconCorriendoAndoL} alt="algo aqui" className="icono" />
      
   {/*   {loading && <Loader />}
      {response && (
        <Message msg="Los datos han sido enviados" bgColor="#198754" />
      )}*/}
    </div>
  );
};

export default LoginForm;

