import React, { useState } from "react";
import Slider from "react-slick";
import "./slider.css";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import banner1 from'../../assets/banner/general/banner1.png'
import banner2 from'../../assets/banner/general/banner2.png'
import banner3 from'../../assets/banner/general/banner3.png'

export default function SimpleSlider() {
  const [currentSlide, setCurrentSlide] = useState(0);

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

  // Funcin para cambiar el ndice del slider cada 5 segundos
  const nextSlide = () => {
    if (currentSlide === settings.slidesToShow - 1) {
      setCurrentSlide(0);
    } else {
      setCurrentSlide(currentSlide + 1);
    }
  };

  // Llama a la funcin `nextSlide` cada 5 segundos
  setInterval(nextSlide, 5000);

  return (
    <Slider {...settings}>
    <div className="slide-img">
       <img src={banner1} alt="Imagen 5" />
      </div>
      <div className="slide-img">
       <img src={banner2} alt="Imagen 5" />
      </div>
      <div className="slide-img">
        <img src={banner3} alt="Imagen 5" />
      </div>
      <div className="slide-img">
        <img src={banner1} alt="Imagen 5" />
      </div>
      <div className="slide-img">
        <img src={banner2} alt="Imagen 5" />
      </div>
    </Slider>
  );
}
