function minutosFaltantes(tiempoDesalida,timeActual) {
  const tiempoActual = new Date(timeActual);
  const tiempoDesalidaDate = new Date(tiempoDesalida);
  const diferencia = tiempoDesalidaDate - tiempoActual;
  const minutosFaltantes = Math.ceil(diferencia / (1000 * 60));
  const horasFaltantes = Math.ceil(minutosFaltantes / 60);
  const diasFaltantes = Math.ceil(horasFaltantes / 24);
  const cero=0

  if (diferencia <= 0) { // si la diferencia es menor o igual a cero, el tiempo ha llegado a cero
   // setMostrarBoton_Inscripcion(true); // cambiamos el valor de Inscripcion a true
    return cero;
  } else if (diasFaltantes >= 2) {
    return diasFaltantes + " dias";
  } else if (horasFaltantes >= 2) {
    return horasFaltantes + " horas";
  } else {
    return minutosFaltantes + " minutos";
  }
}



