import React from 'react'
import { useContext } from "react"
import LoginButton from"./LoginButton.jsx"

import "./initbar.css"
import "./ButtonLogin.css"

import LanguageContext from "../../context/LanguageContext";
import ThemeContext from "../../context/ThemeContext";


export default function Initbar(props) {
  const { theme, handleTheme } = useContext(ThemeContext);
  const {  handleLanguage } = useContext(LanguageContext);


 
  return (
    <nav className={theme}>
        <div className={"link-init" + theme}>
        <ul className={props.theme}>
 
         <li className="switche"> 
      <input id="language-toggle" className="check-toggle check-toggle-round-flat" type="checkbox" onClick={handleLanguage}/>
      <label htmlFor="language-toggle" ></label>
      <span className="on">SP</span>
      <span className="off">EN</span>
  </li>
      <li>
      <label className={"switch"}>
    <input type="checkbox" value={theme} onClick={handleTheme}/>
    <span className={"slidersun"+theme}></span>
    </label>
      </li>
      
      
       <li>
       <LoginButton/>
      </li>
         </ul>
     

      </div>
     
       
      </nav>
  );
}